
.mu-appbar{
    text-align: left;
    position: fixed;
    top: 0;
}
.topic{
    margin-top: 56px;
}
.table{
    background: #369219;
    padding: 2px 4px;
    color: #fff;
    font-size: 12px;
}
.topic-title{
    font-size: 22px;
    font-weight: 700;
}
.topic-inset{
    margin-left: 0;
}
.topic-item{
    display: inline-block;
}
.markdown-text h2{
    padding-bottom: 10px;
    margin-bottom: 1px solid black;
}
.topic-content {
      padding: 1rem;
      border-top: 1px solid #e5e5e5;
      margin-bottom: 2rem;
      text-align: left;
}
.topic-content h1,
  .topic-content h2,
  .topic-content h3,
  .topic-content h4,
  .topic-content h5,
  .topic-content h6 {
      font-weight: 700;
}
.topic-content p {
      font-size: 15px;
      line-height: 1.7em;
      overflow: auto;
}
.topic-content h2 {
      font-size: 26px;
      margin: 30px 0 15px;
      border-bottom: 1px solid #eee;
}
.topic-content h3 {
      margin: 30px 0 15px;
      border-bottom: 1px solid #eee;
      font-size: 24.5px;
}
.topic-content pre {
      font-family: Monaco, Menlo, Consolas, "Courier New", monospace;
      font-size: 14px;
      border-radius: 0;
      padding: 1rem;
      border: 0;
      margin: 1rem 0;
      border-width: 1px 0;
      background: #f7f7f7;
}
.topic-content ul {
      list-style-type: disc;
      margin: 0 0 1rem 2rem;
}
.topic-content ol {
      list-style: decimal;
      margin-left: 2em;
}
.topic-content li {
      line-height: 2.5rem;
}
.topic-content a {
      display: block;
}
.topic-content img {
      height: auto;
      max-width: 100%;
      vertical-align: middle;
      border: 0;
}

  /*评论区样式*/
.replies>li {
    padding: 1rem;
    text-align: left;
}
.replies>li:first-child {
    background-color: #f6f6f6;
}
.reply {
    border: 1px solid #f0f0f0;
    outline:none;
}
.reply>.msg {
    display: flex;
    justify-content: space-between;
    padding-bottom: 1rem;
}
.reply>p {
    margin-bottom: 2rem;
}
.author>img {
    width: 3rem;
    height: 3rem;
    /*border-radius: 50%;*/
    vertical-align: top;
}
.author>.name {
    color: #666;
    font-weight: 700;
}
.author>.timer {
    color: #08c;
    /*font-weight: 700;*/
}
.msg>.ups {
    display: flex;
    align-items: center;
    color: #009688;
}
.ups>span {
    margin-left: 0.5rem;
}
.reply {
    width: 100%;
}
.textsms {
    margin-left: 1rem;
    color: #009688;
    height: 24px;
    font-size: 22px;
}
.reply_show {
    animation: reply_show 1s ease;
}
@keyframes reply_show {
0% {
        opacity: 0;
}
100% {
        opacity: 1;
}
}



